// slider reviews
var rev = $('.rev_slider');
rev.on('init', function(event, slick, currentSlide) {
  var
    cur = $(slick.$slides[slick.currentSlide]),
    next = cur.next(),
    prev = cur.prev();
  prev.addClass('slick-sprev');
  next.addClass('slick-snext');
  cur.removeClass('slick-snext').removeClass('slick-sprev');
  slick.$prev = prev;
  slick.$next = next;
}).on('beforeChange', function(event, slick, currentSlide, nextSlide) {
  // console.log('beforeChange');
  // console.log(slick.$prev, slick.$next);
  var
    cur = $(slick.$slides[nextSlide]);
    slick.$prev.removeClass('slick-sprev');
    slick.$next.removeClass('slick-snext');
    var next = cur.next(),
    prev = cur.prev();
  prev.prev();
  prev.next();
  prev.addClass('slick-sprev');
  next.addClass('slick-snext');
  slick.$prev = prev;
  slick.$next = next;
  cur.removeClass('slick-next').removeClass('slick-sprev');
});

rev.slick({
  speed: 1000,
  arrows: true,
  dots: false,
  focusOnSelect: true,
  prevArrow: '<button class="prev-btn"> prev</button>',
  nextArrow: '<button class="next-btn"> next</button>',
  infinite: true,
  centerMode: true,
  slidesPerRow: 1,
  slidesToShow: 1,
  slidesToScroll: 1,
  centerPadding: '0',
  touchThreshold: 10,
  responsive: [
    {
      breakpoint: 768, 
      settings: {
        adaptiveHeight: true
      }
    }
  ]
});
// slider reviews end 

$('.js-inputfile').on('change', function (event) {
  var label_text;
  if (event.target.files.length) {
    label_text = event.target.files[0].name;
    $('.file-feedback-name').text(label_text).addClass('is-active');
  }
});

$('body').on('click', '.numb-box-minus, .numb-box-plus', function(e){
  e.preventDefault();
    var parentItem = $(this).closest('.calc-numb-box');
    var input = $(parentItem).find(".numb-val");
    var step = parseInt(input.attr("data-step"));

    var newVal = parseInt(input.val());
    newVal = $(this).is(".numb-box-plus")
        ? newVal + step
        : newVal - step
    ;
    if(newVal >= 1) {
      input.val(newVal);
      input.trigger("change");
    }


    e.preventDefault();
});



