import '@babel/polyfill';
import svg4everybody from 'svg4everybody';

import $ from 'jquery';

import 'uikit';
import Parallax from 'parallax-js';



try {

	function initParalax(destroyParam) {

		if($('#scene').length > 0)  {
			var scene = document.getElementById('scene');
			var parallaxInstance = new Parallax(scene);
		}
		
		if($('#medicalScene').length > 0) {
			var medicalScene = document.getElementById('medicalScene');
			var parallaxInstanceMedical = new Parallax(medicalScene);
		} 

		if(destroyParam) {

			if($('#scene').length > 0) {
				parallaxInstance.destroy();
				parallaxInstance = null;  
			}

			if($('#medicalScene').length > 0) {
				parallaxInstanceMedical.destroy();
				parallaxInstanceMedical = null;  
			}

		}
	}


	function loadPageParalax() {
	 var windowWidth = $(window).outerWidth(); 

		if(windowWidth >= 768 ){
			if($('#scene') || $('#medicalScene')) {
				initParalax();
			}
		}


	}//end loadPageParalax
	window.addEventListener("load", loadPageParalax);


	function resizePageParalax() {
	  var windowWidth = $(window).outerWidth(); 

		if(windowWidth <= 767 ){
			if($('#scene') || $('#medicalScene')) {
				initParalax(true);
			}			
		}	  
	  if(windowWidth >= 768 ){
				if($('#scene') || $('#medicalScene')) {
					initParalax();
				}
	  }
	  
		
	}//end resizePageParalax
	window.addEventListener("resize", resizePageParalax);

} catch (e) {}
